.contact-wrapper {
  @apply relative lg:w-10/12 mx-auto my-12 md:my-24 px-6 bg-transparent;

}

.contact-wrapper h2 {
  @apply top-24;
}

.contact-inner { 
  @apply flex flex-col flex-wrap items-center justify-center;
}
.contact-text {
  @apply relative flex flex-col items-center w-full max-w-[365px] h-fit bg-waterloo-900 rounded-tr-full rounded-tl-full pt-32 px-12;
  p {
    @apply text-copperfield-50 text-center; 
  }
  h3 {
    @apply text-copperfield-50 bg-transparent p-0 mt-4; 
  }
  .btn-rounded {
    @apply top-12 w-24 h-24 border-0;
    svg  {
      @apply stroke-waterloo-900 transition-all duration-150 p-6 mr-1 mt-1 z-[2];
      path {
        @apply stroke-waterloo-900 transition-all duration-150;
        transition-timing-function: cubic-bezier(.17,.67,.83,.67);
        stroke-width: 2px;
      }
    }
    &::before {
      content: '';
      width: 4.85rem;
      height: 4.85rem;
      @apply absolute mx-auto p-6 bg-copperfield rounded-full z-[1] transition-all duration-150;
      transition-timing-function: cubic-bezier(.17,.67,.83,.67);
  
    }
    &::after {
      content: '';
      @apply absolute inset-0 border-[10px] border-copperfield-50 rounded-full z-[0];
    }
    &:hover {
      &::before { 
        @apply -translate-y-2 transition-all duration-150;
        box-shadow: 0 0.5em 0.5em -0.4em theme(colors.copperfield.500);
        transition-timing-function: cubic-bezier(.17,.67,.83,.67);
      }
      svg {
        @apply -translate-y-2;
        path {
          @apply stroke-copperfield-50;
        }
      } 
    } 
  }
  &::after {
    content: ' ';
    @apply absolute border border-copperfield -inset-x-4 -inset-y-3 rounded-tr-full rounded-tl-full;

  }
}
